<template>
	<div class="loader-container" id="loader">
		<div class="loader"></div>
	</div>
</template>

<script>
  export default {
    data: () => ({
    }),
	methods: {
		loaderFn() {				
			setTimeout(function() {
				document.getElementById("loader").style.display = "none";
			}, 1000);				
		}
	},
	beforeMount(){
		this.loaderFn()
	}
  }
</script>
